* {
  font-family:Helvetica, Arial, Helvetica, sans-serif
}
body {
  padding: 60px;
}

input{
  width: 75% ;
  font-size: 1.2rem;
  padding: 5px;
  margin: 15px;
  color: blue;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}